import clothesOne from "../imagesWork/clothesOne.jpg";
import clothesThree from "../imagesWork/clothesThree.jpg";
import clothesFour from "../imagesWork/clothesFour.jpg";
import clothesSix from "../imagesWork/clothesSix.jpg";
import clothesSeven from "../imagesWork/clothesSeven.jpg";
import clothesEight from "../imagesWork/clothesEight.jpg";
import clothesNine from "../imagesWork/clothesNine.jpg";
import clothesTen from "../imagesWork/clothesTen.jpg";
import clothesEleven from "../imagesWork/clothesEleven.jpg";
import clothesTwo from "../imagesWork/clothesTwo.jpg"
import clothesTvelv from "../imagesWork/clothesTvelv.jpg"
import clothesThirteen from "../imagesWork/clothesThirteen.jpg"
import React from "react";
import SliderAll from "../slider";







const Dresses = ({lang}) =>{
    const allImg = [
        clothesOne,
        clothesTwo,
        clothesThree,
        clothesFour,
        clothesSix,
        clothesSeven,
        clothesEight,
        clothesNine,
        clothesEleven,
        clothesTvelv,
        clothesThirteen
    ]
    return(
        <div className="wrapWorkComponents">
            <h2>{lang==="ru" ? `Одежда` : 'Clothes'}</h2>

            <div className="itemWrap">
                <SliderAll img={allImg}/>
            </div>

        </div>
    )
}
export default Dresses;
