import React, {useContext} from "react";
import ThemeContext from "../../context";
import "./footer.css";
import "./responsive.css"
import inst from "../../images/inst.png";
import phone from "../../images/phone.png";
import post from "../../images/post.png";
import {NavLink} from "react-router-dom";
const Footer = () =>{
    const {lang} = useContext(ThemeContext);
   return(
       <div className="footer">
           <div className="wrap__footer">
               <div className="footer__item">
                  <p> {lang==="ru" ? `Подпишись на меня` : `Follow me`}</p>
                   <a href="https://instagram.com/redbeastanna?igshid=1jlps1y5k9xd7"><img src={inst} alt=""/></a>
               </div>
               <div className="footer__item">
                    <p> {lang==="ru" ? `Свяжись со мной` : `Call me`}</p>
                   <a href="tel:+1234567890"><img src={phone} alt="phone"/> </ a>
               </div>
               <div className="footer__item">
                   <p> {lang==="ru" ? `Отправь сообщение` : `Send me message`}</p>
                   <a href="mailto:strelchenkoarur@gmail.com"><img src={post} alt="post"/> </ a>
               </div>
           </div>
           <div className="wrap__footer">
               <ul>
               <li><NavLink to="/main" className="linksFooter" activeClassName="selected" >
                   {lang==="ru" ? `Обо мне` : 'About me'}
               </NavLink>
               </li>
               <li><NavLink to="/work" className="linksFooter" activeClassName="selected">
                   {lang==="ru" ? `Мои роботы` : 'My work'}
               </NavLink>
               </li>
               <li><NavLink to="/project" className="linksFooter" activeClassName="selected">
                   {lang==="ru" ? `Проекты` : `Projects`}
               </NavLink>
               </li>
               <li><NavLink to="/contact" className="linksFooter" activeClassName="selected">
                   {lang==="ru" ? `Контакты` : `Contact`}
               </NavLink>
               </li>
               </ul>
           </div>
       </div>
   )
}
export default Footer;