import emailJs from "emailjs-com";
import './contact.css';
import {useContext, useEffect, useState} from "react";
import {Button,  TextField} from "@material-ui/core";
import ThemeContext from "../../context";
import Footer from "../footer/footer";




const Contact = () =>{
    const {lang} = useContext(ThemeContext);


    const [nameUser,setNameUser] = useState([]);
    const [email,setEmail] = useState([]);
    const [message,setMessage]=useState([])

    const [checkEmail,setCheckEmail] =useState(false);

    const [validName,setValidName] = useState(null)
    const [validEmail,setValidEmail] = useState(null)
    const [validMessege,setValidMessege] = useState(null)

    const [form,setForm] = useState(null);
    const [color,setColor] = useState(null);

    
    useEffect(()=>{const raw = localStorage.getItem('name') || []
        setNameUser(JSON.parse(raw));},[])
    useEffect(()=>{localStorage.setItem("name",JSON.stringify(nameUser));},[nameUser])


    useEffect(()=>{const raw = localStorage.getItem('email') || []
        setEmail(JSON.parse(raw))},[])
    useEffect(()=>{localStorage.setItem("email",JSON.stringify(email)); },[email])


    useEffect(()=>{const raw = localStorage.getItem('messege') || []
        setMessage(JSON.parse(raw))},[])
    useEffect(()=>{localStorage.setItem("messege",JSON.stringify(message));},[message])


    const sendEmail=(e)=> {
        e.preventDefault();

        if (nameUser.length>4 && message.length>15 && checkEmail) {

            emailJs.sendForm('service_8zy77vd', 'template_sv8z71j', e.target,
                'user_xfg6Lq0YEVmdSv9KquWrU')
                .then((result) => {
                    console.log(result.text);
                }, (error) => {
                    console.log(error.text);
                });

            setForm(lang==="ru" ? `Отправелено` : `Sent`);
            setColor('validForm');
            setNameUser('');
            setEmail('');
            setMessage('');

            e.target.reset();
        }else {
            setColor('notValidForm');
            setForm(lang==="ru" ? `Заполните пожалуйста коректно` : `Please write correct`);

        }
    }

const handleNameChange = (e) =>{

 switch(e.target.name){
    case "name" :{
        setNameUser(e.target.value)

        let re = /[^a-zA-Z0-9]+/g;
        if (nameUser.length<3 || re.test(String(nameUser))){
            setValidName(lang==="ru" ? `Правильно напишите свое имя,должно быть больше чем 3 символа,только буквы и цифры` : `Write please correct your name must be more than 3 letters,only letters and number`)
        }else{
            setValidName('')
        }
    }break;
    case "email" :{
        setEmail(e.target.value);
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if(!re.test(String(email).toLowerCase())){
            setValidEmail(lang==="ru" ? `Правильно напишите ваш email` : `Please write correct your email`)
            setCheckEmail(false);
        }else{
            setValidEmail('')
            setCheckEmail(true);
        }
    }break;
    case "messege" :{
        setMessage(e.target.value);
        if (message.length<15){
            setValidMessege(lang==="ru" ? `Правильно напишите ваше сообщение, должно быть больше чем 15 символов` : `Write please correct your message must be more than 15 letters`)
        }
        else {
            setValidMessege('')
        }
    }break;
}

}

    return(

        <section className="contactWrap">

            <h2>

                {lang==="ru" ? `Давай поговрим` : `Let's talk`}

            </h2>
            <form className="contact-form" onSubmit={sendEmail}>
                <span className="notValidForm">{validName}</span>
                <TextField
                    label={lang==="ru" ? `Имя` : `Name`}
                    multiline
                    rowsMax={10}
                    rosMin={1}
                    name="name"
                    className="form__item"
                    value={nameUser}
                    onChange={handleNameChange}
                />
                <span className="notValidForm">{validEmail}</span>
                <TextField
                    label="Email"
                    multiline
                    rowsMax={10}
                    name="email"
                    className="form__item"
                    value={email}
                    onChange={handleNameChange}
                />

                <span className="notValidForm">{validMessege}</span>
                <TextField
                    id="standard-textarea"
                    label={lang==="ru" ? `Сообщение` : `Message`}
                    multiline
                    name="messege"
                    className="form__item"
                    value={message}
                    onChange={handleNameChange}
                />
                <div>
                    <p className={color}>{form}</p>
                </div>
                <Button variant="outlined" value="Send" className="sendMessage" type="submit">
                    {lang==="ru" ? `Отправить` : `Send`}
                </Button>
            </form>

        </section>
    );
}
export default Contact;