import React, {useContext} from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import {NavLink} from "react-router-dom";
import ThemeContext from "../../context";

export default function SimpleMenu() {
    const [anchorEl, setAnchorEl] = React.useState(null);
   const {lang,setLang} = useContext(ThemeContext);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <div>
            <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
                {lang==="ru" ? `Меню` : 'Menu'}
            </Button>
            <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={handleClose}><NavLink to="/main" className="links" activeClassName="selected" >
                    {lang==="ru" ? `Обо мне` : 'About me'}
                </NavLink></MenuItem>
                <MenuItem onClick={handleClose}><NavLink to="/work" className="links" activeClassName="selected">
                    {lang==="ru" ? `Мои роботы` : 'My work'}
                </NavLink></MenuItem>
                <MenuItem onClick={handleClose}><NavLink to="/project" className="links" activeClassName="selected">
                    {lang==="ru" ? `Проекты` : `Projects`}
                </NavLink></MenuItem>
                <MenuItem onClick={handleClose}><NavLink to="/contact" className="links" activeClassName="selected">
                    {lang==="ru" ? `Контакты` : `Contact`}
                </NavLink></MenuItem>
                <MenuItem><div className="phoneChangeLang" onClick={()=> lang==="en" ? setLang("ru") : setLang("en")}>
                    <p>{lang.toUpperCase()}</p>
                </div>
                </MenuItem>
            </Menu>
        </div>
    );
}


