    import bcg from "../../images/phmm.jpg"
    import bcg2 from "../../images/ph2.jpg"
    import './main.css'
    import './responsive.css'

    import {useContext} from "react";
    import ThemeContext from "../../context";
    export const Main = () =>{
        const {lang} = useContext(ThemeContext);
    return(

  

        <main className="main">
      <article className="main__details">
          <div className="smallPhone">
          <div className="wrapInfo">
          <p className="detail main__item">
              {lang==="ru" ? `НЕМНОГО ОБО МНЕ` : 'SOME DETAILS ABOUT ME'}
          </p>
          <h1 className="aboutAnn main__item">
              {lang==="ru" ? `Про Анну Валеску` : 'About Anna Valeska'}
          </h1>
              <div className="instagram_wrap main__item">
                  <a href="https://instagram.com/redbeastanna?igshid=1jlps1y5k9xd7" className="main__instagram" >

                      {lang==="ru" ? `Подпишись на Instagram` : 'Subscibe me Instagram'}
                      <span className="instagram__iconlink">&#8594;</span>
                  </a>
              </div>
          </div>
          </div>


          <article className="main__more">

              <div className="more__item">
                  <div className="item-text">
                  <p>
                      {lang==="ru" ? `Моё имя Анна Валеска, рада с Вами познакомиться.
                      Я начинающий дизайнер и художница.
                      Развитие в профессиональной сфере началось в 2016 году, когда Я поступила в университет искусств.
                      С того момента проходила множество стажировок и курсов у представителей профессии, что и сподвигло меня создавать “своё”.
                      Хочу объяснить что представляет собой Анна Валеска.
                      Я не против современной моды и искусства но и не всеми руками за классику.
                      Я не стану утверждать, что лучше импрессионизма и флэпперов, например, ничего нет.
                     `

                          :
                          `
                          My name is Anna Valeska, I am glad to meet you.
                        I am an aspiring designer and artist. 
                          The development in the professional field began in 2016, when I entered the University of the Arts.                      
                       Since that moment, I have undergone many internships and courses with representatives of the profession, which prompted me to create “my own”.
                          I want to explain what Anna Valeska is. I am not against modern fashion and art, but not with all hands for the classics. 
                          I will not argue that there is nothing better than impressionism and flappers, for example. 
                          
                          `}

                  </p>
                    </div>

                  <div className="more__item bcg__more">
                      <img src={bcg} alt=""/>
                  </div>

                  <div className="more__item bcg__more">
                      <img src={bcg2} alt=""/>
                  </div>
                  <div className="item-text">

                  <p>
                      {lang==="ru" ? `
                      Анна Валеска это созданный мною вкус и взгляд на прекрасное.
                      Каждого художника отличает то чем он вдохновляется при создании своей работы, поэтому бесспорно Мы все разные и уникальные.
                      Здесь будут собраны мои работы начиная с 2016 года но и также мои новые проекты.
                      У меня будет возможность оттачивать свои умения, а у Вас наблюдать за созданием новых замыслов.
                      Тут будет мой опыт, достижения и ошибки.`

                          :
                          `
                          
                          Anna Valeska is a taste and a view of beauty created by me.
                           Each artist is distinguished by what he is inspired by when creating his work, therefore, undoubtedly, We are all different and unique.
                            Here will be collected my works from 2016 but also my new projects. 
                            I will have the opportunity to hone my skills, and you will have the opportunity to observe the creation of new ideas. 
                            Here will be my experience, achievements and mistakes.
                          `}

                  </p>
                  </div>

              </div>


          </article>




      </article>

        </main>
    

    );
    }
