import {NavLink} from "react-router-dom";
import {useContext} from "react";
import ThemeContext from "../../context";

const NotFound = () =>{
    const {lang} = useContext(ThemeContext);
    return(
        <div style={{color:"gold",marginTop:"10rem",textAlign:"center"}} >
            <h2 > {lang==="ru" ? `Не найдено,` : 'Not found 404'}
            </h2>
            <h4>
                {lang==="ru" ? `Пожалуйста введите коректно адрес` : 'Please write correct address'}
            </h4>
        </div>
    );
}
export default NotFound;